import { customElement, property, query } from 'lit/decorators.js';
import { html, TemplateResult } from 'lit';
import { Base } from '../base';
import { TabData, ZwcTabsTemplate } from './zwc-tabs-template';
import { TabsAlignment } from './zwc-tabs-renderer';

import './zwc-tabs-template';
import './zwc-tabs-renderer';

/**
 * ZWC Tab Web component.
 * @usage 
 * 
    <zwc-tabs>
      <zwc-tabs-template>
        <template tab="tab-1">
          <div class="label">Header Tab 1</div>
          <div class="body">
            Body content for tab 1.
          </div>
        </template>      
        <template tab="tab-2">
          <div class="label">Header Tab 2</div>
          <div class="body">
            Body content for tab 2.
          </div>
        </template>
        <template tab="tab-3">
          <div class="label">Header 3</div>
          <div class="body">
            Body content for tab 3.
          </div>
        </template>
      </zwc-tabs-template>
    </zwc-tabs>
  <!-- or -->
    <zwc-tabs
      data='[{"id":"tab-2","label":"Header Tab 2","body":"Body content for tab 2."},{"id":"tab-3","label":"Header 3","body":"Body content for tab 3."}]'
      ></zwc-tabs>
 */
@customElement('zwc-tabs')
export class ZwcTabs extends Base {
  @property({ type: Object })
  data: TabData[] = [];

  @property({ type: String })
  layout: TabsAlignment = 'stretch';

  @property({ type: String, attribute: 'icons' })
  icons = '';

  @property({ type: Boolean, attribute: 'scroll' })
  scrollToTab = true;

  @query('zwc-tabs-template')
  collector!: ZwcTabsTemplate;

  protected override render(): TemplateResult {
    return html`${this.data.length > 0
      ? html` <zwc-tabs-renderer
          .tabsData=${this.data}
          .icons=${this.icons}
          .layout=${this.layout}
          .scrollToTab=${this.scrollToTab}
        >
        </zwc-tabs-renderer>`
      : ''}`;
  }

  protected firstUpdated(): void {
    if (this.collector) {
      this.collector.addEventListener('change', e => this.handleChange(e));
    }
  }

  protected handleChange(ev: Event): void {
    this.data = (ev as CustomEvent).detail as TabData[];
    this.requestUpdate();
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'zwc-tabs': ZwcTabs;
  }
}
